body {
  /* OFF Theme */
  --opacity: 0.2;

  /*Dark Theme Colors */

  --bgMainDark: hsl(240, 25%, 10%);
  --bgDrumMachineDark: hsl(240, 18%, 14%);
  --shadowBtnDark: 0px 0px 5px 1.5px rgb(83, 93, 116);
  --fontColorDark: white;
  --bgInactiveBtnDark: hsl(225, 36%, 7%);
  --shadowHoverBtnDark: 0px 0px 7px -2px rgb(255, 255, 255) inset;

  /* Light Theme Colors */

  --bgMainLight: hsl(240, 15%, 95%);
  --bgDrumMachineLight: hsl(240, 18%, 75%);
  --shadowBtnLight: 0px 0px 3px 1.2px hsl(240, 25%, 10%);
  --bgInactiveBtnLight: hsl(232, 10%, 50%);
  --shadowHoverBtnLight: 0px 0px 7px -2px rgb(15, 15, 15) inset;

  /* Button effects */
  --bgActiveBtn: hsl(43, 100%, 58%);

  --focusBtnDark: 0.05rem solid white;
  --focusBtnLight: 0.05rem solid rgb(0, 0, 0);

  --displayActive: hsl(68, 90%, 59%);

  /* --headerGridDesktop:  */
  /* --headerGridMobile:  */

  /* Light Theme Colors */
  --bgMainLight: hsl(240, 18%, 79%);
  --bgDrumMachineLight: hsl(240, 18%, 14%);
  --bgDrumPadsLight: hsl(225, 36%, 7%);
  --shadowDrumPadsLight: 0px 0px 5px 2px rgb(0, 0, 0);

  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.display {
  display: flex;
  justify-content: space-between;
  color: black;
  font-family: open sans;
  font-weight: 500;
  font-size: 1.5rem;
  background-color: var(--displayActive);
  padding: 0.4em 0.8em;
  border-radius: 5px;
  border: none;
  width: 100%;
  margin: 0;
  box-shadow: 0 0 0.6rem 0.2rem rgba(0, 0, 0, 0.5) inset;
}

.hiddenDisplay {
  opacity: 0;
}

@media (max-height: 650px) {
  .display {
    font-size: 1rem;
  }
}

.drum-pads {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1em;
    gap: 1em;
    width: 100%;
    height: 60%;
    background-color:transparent;  
}
@media (max-width: 420px) and (min-height: 560px) {
    .drum-pads {
      height: 60%;
      /* max-height: 40rem; */
  }
  }
  
@media (max-width: 420px) and (min-height: 660px) {
    .drum-pads {
      height: 45%;
      /* max-height: 40rem; */
  }
  }
  
.darkBtn {
  background-color: var(--bgInactiveBtnDark);
  box-shadow: var(--shadowBtnDark);
}
.darkBtnHover {
  box-shadow: var(--shadowHoverBtnDark);
}
.darkBtnFocus {
  box-shadow: 0px -0.2rem 0rem -0.1rem white inset, var(--shadowBtnDark);
  outline: none;
}
.lightBtn {
  background-color: var(--bgInactiveBtnLight);
  box-shadow: var(--shadowBtnLight);
}
.lightBtnHover {
  box-shadow: var(--shadowHoverBtnLight);
}
.lightBtnFocus {
  border-bottom: var(--focusBtnLight);
  box-shadow: 0px -0.2rem 0rem -0.1rem rgb(255, 255, 255) inset,
    var(--shadowBtnLight);
  outline: none;
}

.drum-pad {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  border: none;
  border-radius: 0.7em;
  min-height: 2rem;
  max-height: 4.5rem;
}

.hoverScale .key-code {
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}

.key-code {
  color: var(--fontColorDark);
  text-align: center;
  font-size: 1rem;
}

.playing {
  -webkit-animation: colorFade 0.2s ease-out, shadowFade 0.2s ease-out;
          animation: colorFade 0.2s ease-out, shadowFade 0.2s ease-out;
}

@-webkit-keyframes colorFade {
  0% {
    background: var(--bgInactiveBtn);
  }
  /* Adding a step in the middle */
  50% {
    background: var(--bgActiveBtn);
  }
  100% {
    background: var(--bgInactiveBtn);
  }
}

@keyframes colorFade {
  0% {
    background: var(--bgInactiveBtn);
  }
  /* Adding a step in the middle */
  50% {
    background: var(--bgActiveBtn);
  }
  100% {
    background: var(--bgInactiveBtn);
  }
}
@-webkit-keyframes shadowFade {
  from {
    box-shadow: 0px 0px 5px 2px;
  }
  to {
    box-shadow: 0 0 1rem 0px #ffc600;
  }
}
@keyframes shadowFade {
  from {
    box-shadow: 0px 0px 5px 2px;
  }
  to {
    box-shadow: 0 0 1rem 0px #ffc600;
  }
}

.header {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  color: var(--fontColorDark);
  width: 100%;
  padding-top: 0.5rem;
  padding-bottom: 1rem;
}

.title {
  justify-self: start;
  align-self: center;
  font-size: 1.2em;
  grid-column: 1 / span 2;
  font-weight: 400;
  padding-left: 0;
  margin: 0;
}

.author {
  font-family: Waiting for the sunrise;
  font-size: 1.3em;
  grid-column: 4 / span 2;
  font-weight: 500;
  justify-self: end;
  align-self: center;
}

