.darkTheme {
  background-color: var(--bgInactiveBtnDark);
  box-shadow: var(--shadowBtnDark);
}
.darkThemeHover {
  box-shadow: var(--shadowHoverBtnDark);
}

.darkThemeFocus {
  box-shadow: 0px -0.2rem 0rem -0.1rem white inset, var(--shadowBtnDark);
  outline: none;
}

.lightTheme {
  background-color: var(--bgInactiveBtnLight);
  box-shadow: var(--shadowBtnLight);
}
.lightThemeHover {
  box-shadow: var(--shadowHoverBtnLight)
}
.lightThemeFocus {
  box-shadow: 0px -0.2rem 0rem -0.1rem white inset, var(--shadowBtnLight);
  outline: none;
}


.controls {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.ctrlBtn {
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--fontColorDark);
  border-radius: 0.7em;
  cursor: pointer;
  border: none;
}

.ctrlBtnHover .power {
  transform: scale(0.9)
}

.ctrlBtnHover .text {
  transform: scale(0.9)
}

.active{
  background-color: var(--bgActiveBtn);
}

.ctrlWrapper {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1em;
  padding-top: 1rem;
}

.power {
  width: 1.6rem;
}
.iconOff {
  opacity: 1 !important;
}


.volumeWrapper {
    width: 100%;
}

input[type="range"]:focus::-webkit-slider-thumb {
  box-shadow: 0px -0.2rem 0rem -0.1rem rgb(255, 255, 255) inset; 
}

input[type="range"] {
  -webkit-appearance: none; /* Hides the slider so that custom slider can be made */
  width: 100%; /* Specific width is required for Firefox. */
  background: transparent; /* Otherwise white in Chrome */
  margin: 18px 0;
  padding: 0.5rem 0;
}

input[type="range"]::-webkit-slider-thumb {
  -webkit-appearance: none;
}

input[type="range"]:focus {
  outline: none;
  
  /* Removes the blue border. You should probably do some kind of focus styling for accessibility reasons though. */
}


input[type=range]::-webkit-slider-thumb {
    -webkit-appearance: none;
    border: 1px solid #000000;
    height: 20px;
    width: 30px;
    border-radius: 3px;
    background: var(--bgActiveBtn);
    cursor: pointer;
    margin-top: -6px; /* You need to specify a margin in Chrome, but in Firefox and IE it is automatic */
    box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d; /* Add cool effects to your sliders! */
  }
  
  /* All the same stuff for Firefox */
  input[type=range]::-moz-range-thumb {
    box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
    border: 1px solid #000000;
    height: 20px;
    width: 30px;
    border-radius: 3px;
    background: #ffffff;
    cursor: pointer;
  }
  
  /* All the same stuff for IE */
  input[type=range]::-ms-thumb {
    box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
    border: 1px solid #000000;
    height: 20px;
    width: 30px;
    border-radius: 3px;
    background: #ffffff;
    cursor: pointer;
  }

  input[type=range]::-webkit-slider-runnable-track {
    width: 100%;
    height: 8.4px;
    cursor: pointer;
    box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
    background: var(--bgInactiveBtnDark);
    border-radius: 1.3px;
    border: 0.2px solid #010101;
  }
  
  input[type=range]:focus::-webkit-slider-runnable-track {
    background: var(--bgInactiveBtnDark);
  }
  input[type=range]::-moz-range-track {
    width: 100%;
    height: 8.4px;
    cursor: pointer;
    box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
    background: #3071a9;
    border-radius: 1.3px;
    border: 0.2px solid #010101;
  }
  
  input[type=range]::-ms-track {
    width: 100%;
    height: 8.4px;
    cursor: pointer;
    background: transparent;
    border-color: transparent;
    border-width: 16px 0;
    color: transparent;
  }
  input[type=range]::-ms-fill-lower {
    background: var(--bgInactiveBtnDark);
    border: 0.2px solid #010101;
    border-radius: 2.6px;
    box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  }
  input[type=range]::-ms-fill-upper {
    background: var(--bgInactiveBtnDark);
    border: 0.2px solid #010101;
    border-radius: 2.6px;
    box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  }
 
  input[type=range]:focus::-ms-fill-lower {
    background: var(--bgInactiveBtnDark);
  }
  input[type=range]:focus::-ms-fill-upper {
    background: var(--bgInactiveBtnDark);
  }

  @media (max-height: 650px) {
      .ctrlBtn {
          height: 2.5rem;
      }

      .power {
        width: 1.5rem;
      }
  }

  @media (max-width: 420px) {
    .ctrlBtn {
      height: 3.6rem;
    }
  }

  @media (max-width: 420px) and (min-height: 560px) {
    
  }

  @media (max-width: 420px) and (min-height: 660px) {
    input[type="range"] {
      padding-top: 2rem;
      margin: 0;
    }
  }
  
  
  